export default {
  head() {
    return {
      title: 'Cryptography | Atbash ',
      meta: [
        {
          hid: 'Cryptography | Atbash ',
          name: 'Cryptography | Atbash',
          content: 'Cryptography | Atbash'
        }
      ]
    }
  },
  data: () => ({
    atbash: '',
    atbash_result: '',
    transparent: 'rgba(255, 255, 255, 0)',
    blogs_load: false,
    news_load: false,
    posts_load: false,
    selection: 0,
    content: [],
    news: [],
    posts: [],
    nuxt: [],
    selectedItem: 0,
    show: true,
    title: '',
    model: 6,
    rounded: ['0', 'sm', 'md', 'lg', 'xl', 'pill', 'circle'],
    links: [],

    posts_content: 0,
    news_content: 0,

    tags: [],
    load_tags: false,
    total_tags: 0,
    height: '200px',
    blogs_load: false,
    blogs_content: [],
    blogs_total: 0,

    nuxt: [],
    laravel: [],
    vue: [],
    load_nuxt: false,
    load_laravel: false,
    load_vue: false,

    show_nuxt: false,
    show_laravel: false,
    show_vue: false,
    blogs_in_random: [],
    blogs_in_latest: [],
    page: 2
  }),
  layout: 'default',
  methods: {
    clearfields() {
      this.atbash = ''
      this.atbash_result = ''
    },
    atbash_convert() {
      var result = ''
      var str = this.atbash
      str.split('').map(letter => {
        result += this.reverse_letter(letter)
      })
      this.atbash_result = result
    },
    reverse_letter(letter) {
      switch (letter) {
        case 'a':
          return 'z'
        case 'A':
          return 'Z'
        case 'b':
          return 'Y'
        case 'B':
          return 'y'
        case 'C':
          return 'X'
        case 'c':
          return 'x'
        case 'D':
          return 'W'
        case 'd':
          return 'w'
        case 'E':
          return 'V'
        case 'e':
          return 'v'
        case 'F':
          return 'U'
        case 'f':
          return 'u'
        case 'G':
          return 'T'
        case 'g':
          return 't'
        case 'H':
          return 'S'
        case 'h':
          return 's'
        case 'I':
          return 'R'
        case 'i':
          return 'r'
        case 'J':
          return 'Q'
        case 'j':
          return 'q'
        case 'K':
          return 'P'
        case 'k':
          return 'p'
        case 'L':
          return 'O'
        case 'l':
          return 'o'
        case 'M':
          return 'N'
        case 'm':
          return 'n'
        case 'N':
          return 'M'
        case 'n':
          return 'm'
        case 'O':
          return 'L'
        case 'o':
          return 'l'
        case 'P':
          return 'K'
        case 'p':
          return 'k'
        case 'Q':
          return 'J'
        case 'q':
          return 'j'
        case 'R':
          return 'I'
        case 'r':
          return 'i'
        case 'S':
          return 'H'
        case 's':
          return 'h'
        case 'T':
          return 'G'
        case 't':
          return 'g'
        case 'U':
          return 'F'
        case 'u':
          return 'f'
        case 'V':
          return 'E'
        case 'v':
          return 'e'
        case 'W':
          return 'D'
        case 'w':
          return 'd'
        case 'X':
          return 'C'
        case 'x':
          return 'c'
        case 'Y':
          return 'B'
        case 'y':
          return 'b'
        case 'Z':
          return 'A'
        case 'z':
          return 'a'
        // Aa Bb Cc Dd Ee Ff Gg Hh Ii Jj Kk Ll Mm Nn Oo Pp Qq Rr Ss Tt Uu Vv Ww Xx Yy Zz
        // Zz Yy Xx Ww Vv Uu Tt Ss Rr Qq Pp Oo Nn Mm Ll Kk Jj Ii Hh Gg Ff Ee Dd Cc Bb Aa
        default:
          // case letter:
          return letter
      }
    }
  },
  async fetch() {
    const BlogsRandom = await this.$axios.$get(`api/blog/index/10`)
    this.blogs_in_random = BlogsRandom.data

    const Blogslatest = await this.$axios.$get(`api/blog/latest/10`)
    this.blogs_in_latest = Blogslatest.data

    this.load_tags = true
    const tags = await this.$axios.$get(`api/tags`)
    this.load_tags = false
    this.tags = tags.data
    this.total_tags = tags.total
  }
}
